<template>
	<div class="blogDetail">
		<div class="blogDetail_mulu" style="position: fixed;left: 0px;bottom: 10%;z-index: 9999;overflow: hidden;">
			<!--  -->
			<el-popover
				placement="right"
				width="6%"
				trigger="click">
				<div class="tail">
					<ol class="js-toc">
						
					</ol>
				</div>
				<el-button slot="reference"><i class="el-icon-s-operation"></i></el-button>
			</el-popover>
		</div>
		
		<el-row :gutter="10">
			<el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4">
				<div>
					.
				</div>
			</el-col>
			<el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
				<div class="blogDetail_cotent">
					<!--  -->
					<div class="blogDetail_cotent_title">
						<h2>{{blog.title}}</h2>
					</div>
					
					<!--  -->
					<div class="blogDetail_cotent_head">
						<div class="blogDetail_cotent_head_l">
							<img :src="blog.useryonghu.userimage" alt="">
						</div>
						<div class="blogDetail_cotent_head_r">
							<div style="display: flex;justify-content: flex-start;align-items: center;">
								<span>{{blog.useryonghu.username}}</span>
									<div style="margin: 6px;color: orange;">
										<span v-if="0==blog.useryonghu.userlevel">
											<h6></h6>
										</span>
										<span v-if="30==blog.useryonghu.userlevel">
											<h6></h6>
										</span>
										<span v-if="100==blog.useryonghu.userlevel">
											<h6></h6>
										</span>
									</div>
								<div @click="conernclick">
									<el-button type="warning" size="mini" style="margin-left: 5px;" v-if="conernt">+</el-button>
									<el-button type="warning" size="mini" style="margin-left: 5px;" v-else>+</el-button>
								</div>
							</div>
							<div style="display: flex;align-items: flex-end;">
								<span>{{blog.createtime|dateFilter}}</span>
								<!-- <span>{{blog.zangcount}}</span> -->
							</div>
						</div>
					</div>
					<!--  -->
					<div class="blogDetail_cotent_c typo typo-selection js-toc-content">
						<div v-html="blog.content">
							
						</div>
					</div>
					<!--  -->
					<div style="text-align: center;">
						<!-- <el-button type="warning" round></el-button> -->
					</div>
					<!--  -->
					<div style="text-align: right;">
						<el-tag
								v-for="item in blog.blogclassfiys"
								:key="item.classfiyid"
								:type="item.type"
								effect="dark" style="margin: 5px;">
							{{ item.classfiyname }}
						</el-tag>
					</div>
					<!--  -->
					<div style="text-align: right;">
						<el-tag
								v-for="item in blog.blogtags"
								:key="item.tagid"
								:type="item.type"
								effect="dark" style="margin: 5px;">
							{{ item.tagname }}
						</el-tag>
					</div>
					<div style="text-align: left;display: flex;">
						<div style="flex: 1;">
							<img :src="userimg" alt="" style="width: 50px;height: 50px;border-radius:50% ;">
						</div>
						<div style="flex: 9;">
							<el-input
							type="textarea"
							:rows="2"
							placeholder=""
							v-model="textarea"
							maxlength="250">
							</el-input>
							<div style="margin-top: 10px;display: flex;justify-content: flex-end;">
								<el-button type="danger" @click="savecomment(nowuserid,blog.id)" :loading="loading"></el-button>
								<el-button type="primary" style="margin-left: 10px;" @click="reset"></el-button>
							</div>
						</div>
					</div>
					<div style="border-left:10px orange solid ;text-align: left;">
						<h3></h3>
					</div>
					<!--  -->
					<blogcomment :userAndParenetComent="item" v-for="item in comments"></blogcomment>
				</div>
			</el-col>
			<el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4">
				<div>
					.
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script> 
	import "@/assets/tocbot.css"
	import "@/assets/tocbot.min.js"
	import blogcomment from '@/components/blogviewcomponent/blogcomment'
	import {savecomment} from '@/util/requestaxiosutil/savecomment'
	import {showParentComment} from '@/util/requestaxiosutil/showParentComment'
	import {savexinxin} from '@/util/requestaxiosutil/userconernt'
	import {deletexinxin} from '@/util/requestaxiosutil/userconernt'
	import {getAllXinXin} from '@/util/requestaxiosutil/userconernt'
	import {getBlogByIdAndUser} from '@/util/requestaxiosutil/userlikeblog'
	
	export default{
		name:"blogDetail",
		// ,
		// 
		created() {
			let loadingInstance=this.$loading({})
			// idblogcomment
			let blog={};
			blog.id=this.$route.params.blogid;
			getBlogByIdAndUser(JSON.stringify(blog)).then(res=>{
				this.blog=res.data.data;
				
				// id
				// id
				let user=JSON.parse(window.localStorage.getItem("remembermeUser"))||{};
				let userandxinxin={};
				userandxinxin.userid=user.id;
				getAllXinXin(JSON.stringify(userandxinxin)).then(res=>{
					this.xinxinids=res.data.data;
					this.conernt=this.xinxinids.indexOf(this.blog.useryonghu.id)<0;
					loadingInstance.close();
				}).catch(error=>{
					
				})
			}).catch(error=>{
				
			})
			
			
			let parentComent={};
			parentComent.blogid=this.$route.params.blogid;
			let rparentComent=JSON.stringify(parentComent);
			
			// 
			showParentComment(rparentComent).then(res=>{
				this.comments=res.data.data;
			}).catch(error=>{
				
			})
			
			
			
		},
		data(){
			return {
				blog:{},
				textarea:"",
				loading:false,
				comments:[],
				nowuserid:this.$store.state.user.id,
				conernt:true,
				xinxinids:[],
				userimg:this.$store.state.user.userimage
			}
		},
		components:{
			blogcomment
		},
		methods:{
			savecomment(userid,blogid){
				let parentComent={};
				parentComent.userid=userid;
				parentComent.blogid=blogid;
				parentComent.comment=this.textarea;
				this.loading=true;
				savecomment(JSON.stringify(parentComent)).then(res=>{
					this.$message({
						message: '',
						type: 'success'
					});
					this.loading=false;
					//
					showParentComment(JSON.stringify(parentComent)).then(res=>{
						this.comments=res.data.data;
					}).catch(error=>{
						
					})
				}).catch(error=>{
					console.log(error)
					this.loading=false;
				})
			},
			reset(){
				this.textarea="";
			},
			// 
			conernclick(){
				this.conernt=!this.conernt;
				if(!this.conernt){
					// idid
					let user=JSON.parse(window.localStorage.getItem("remembermeUser"))||{};
					let userandxinxin={};
					userandxinxin.userid=user.id;
					userandxinxin.xinxinid=this.blog.useryonghu.id;
					
					console.log(JSON.stringify(userandxinxin))
					savexinxin(JSON.stringify(userandxinxin)).then(res=>{
						console.log(res)
					}).catch(error=>{
						
					})
				}else{
					let user=JSON.parse(window.localStorage.getItem("remembermeUser"))||{};
					let userandxinxin={};
					userandxinxin.userid=user.id;
					userandxinxin.xinxinid=this.blog.useryonghu.id;
					deletexinxin(JSON.stringify(userandxinxin)).then(res=>{
						console.log(res)
					}).catch(error=>{
						
					})
				}
			}
		},
		filters:{
			dateFilter(item){
				let pointFlag=item.indexOf(".");
				return item.substring(0,pointFlag).replace("T","  ");
			}
		},
		updated() {
			let nihao=document.getElementsByClassName("js-toc-content");
			console.log(nihao)
			tocbot.init({
				// Where to render the table of contents.
				tocSelector:'.js-toc',
			// Where to grab the headings to build the table of contents.
				contentSelector:'.typo-selection',
				// Which headings to grab inside of the contentSelector element.
				headingSelector:'h1, h2, h3',
  // For headings inside relative or absolute positioned containers within content.
				hasInnerContainers: true,
			});
		}
	}
</script>

<style lang="less">
	.blogDetail{
		.blogDetail_cotent{
			.blogDetail_cotent_head{
				display: flex;
				.blogDetail_cotent_head_l{
					flex: 1;
					display: flex;
					justify-content: center;
					align-items: center;
					&> img{
						width: 50px;
						height: 50px;
						border-radius: 50%;
					}
				}
				
				.blogDetail_cotent_head_r{
					flex: 9;
					display: flex;
					flex-direction: column;
					align-items: flex-start;
					justify-content: center;
				}
			}
		}
	}
</style>
