<template>
	<div class="register-main">
		<div style="margin-top: 20px;">
			<el-row :gutter="10">
				<el-col :xs="24" :sm="24" :md="14" :lg="14" :xl="14">
					<div class="register-main-left">
						<div class="register-main-left-content">
							<h1></h1>
						</div>
					</div>
				</el-col>
				<el-col :xs="24" :sm="24" :md="10" :lg="10" :xl="10">
					<div class="register-main-right">
						<div class="register-main-right-form">
							<el-card class="box-card">
								<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign"
								 :rules="rules" ref="ruleForm">
								<el-form-item label="" prop="username">
									<el-input v-model="formLabelAlign.username" placeholder="" suffix-icon="el-icon-user" maxlength="10"></el-input>
								</el-form-item>
								<el-form-item label="" prop="email">
									<el-input v-model="formLabelAlign.email" placeholder="" suffix-icon="el-icon-message" maxlength="30"></el-input>
								</el-form-item>
								<el-form-item label="" prop="pasword">
									<el-input v-model="formLabelAlign.password" placeholder="" show-password  maxlength="20"></el-input>
								</el-form-item>
								<el-form-item label="" prop="verification">
									<div style="display: flex;">
										<el-input v-model="formLabelAlign.verification" placeholder=""></el-input>
										<el-button type="success" style="margin-left: 10px;" @click="getverification"  :loading="siginloadflag"></el-button>
									</div>
								</el-form-item>
									<p style="text-align: left;text-indent: 2em;">
										
									</p>
								<el-button type="success" style="width: 100%;" @click="submitForm('ruleForm')"></el-button>
								</el-form>
							</el-card>
							<el-card shadow="never" style="margin-top: 30px;">
								you has account?  
								<span>
									<a href="" style="text-decoration: none;" v-on:click.prevent="gotologin">go to login</a>
								</span>
							</el-card>
						</div>
					</div>
				</el-col>
			</el-row>
		</div>
	</div>
</template>

<script>
	import {loginrequest} from '@/util/requestaxiosutil/loginreques'
	import {registerrequest} from '@/util/requestaxiosutil/registerrequest'
	export default{
		name:"signin",
		data(){
			return{
				labelPosition: 'right',
				siginloadflag:false,
				formLabelAlign: {
				username: "",
				email: "",
				password: "",
				verification:""
				},
				rules:{
					username:[
						{ required: true, message: ''},
						{ min: 3, max: 10, message: ' 3  10 '}	
					],
					email:[
						{ required: true, message: ''},
						{ min: 10, max: 30, message: '306'},
						{ type: 'email', required: true, message: ''}
					],
					password:[
						{ required: true, message: ''},
						{ min: 6, max: 20, message: ' 6 20 ', }
					],
					verification:[
						{ required: true, message: ''},
						{ min: 4, max: 6, message: '' }
					]
				}
				
			}
		},
		methods:{
			gotologin(){
				this.$router.push("/loginandsign")
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
				if (valid) {
					let user={};
					user.username=this.formLabelAlign.username;
					user.userpassword=this.formLabelAlign.password;
					user.useremail=this.formLabelAlign.email;
					user.verification=this.formLabelAlign.verification;
					let data=JSON.stringify(user);
					registerrequest(data)
					.then(res=>{
						this.$message({
							message: '',
							type: 'success'
						});
						window.localStorage.removeItem("remembermeUser");
						window.localStorage.removeItem("usertoken");
						this.$store.dispatch("deleteuser");
						this.$router.push("/loginandsign");
					}).catch(error=>{
						console.log("")
					})
				} else {
					console.log('error submit!!');
				return false;
				}
			});
			},
			getverification(){
				let verification={};
				this.siginloadflag=true;
				this.$message({
					message: '',
					type: 'success'
				});

				verification.email=this.formLabelAlign.email;
				loginrequest(JSON.stringify(verification))
				.then(res=>{
					if(res.data.status==500){
						this.$message.error(res.data.msg);
						this.siginloadflag=false;
						return;
					}
					this.$message({
						message: '',
						type: 'success'
					});
					this.siginloadflag=false;
				}).catch(error=>{
					console.log(error)
					this.$message.error('');
					this.siginloadflag=false;
				})
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.register-main{
		width: 100%;
		height: 100%;
		.register-main-left{
			height: 80vh;
			display: flex;
			align-items: center;
			justify-content: center;
			.register-main-left-content{
				height: 70%;
				width: 60%;
			}
		}
		.register-main-right{
			height: 80vh;
			display: flex;
			align-items: center;
			justify-content: center;
			.register-main-right-form{
				// margin-top: -100px;
			}
		}
	}
</style>
